import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import './header.css';
import LoggedHeader from './LoggedHeader';
import axios from 'axios';
import logo from './../Images/logo.png'


function Header(props) {
    const [isNotLogged, setIsNotLogged] = useState(true);
    const [token, setToken] = useState();
    const [loggedIn, setLoggedIn] = useState(false);

    let tokens = null;

    const auth = data => {
        window.SE.authenticate({
            success: (data) => {
                tokens = data;
                setToken(tokens.accessToken);
                props.getUsersToken(tokens.accessToken);
                setLoggedIn(true);
            },
            error: (data) => {
                alert('Я не получил доступ :(');
            },
        });
    }
    useEffect(() => {
        window.SE.init({
            clientId: 19689,
            key: 'yNew8uNfBOS3s)e1w4XQaA(',
            channelUrl: 'https://zhen1asemen1uk.github.io/',
            complete: () => {
                setIsNotLogged(false);
            }
        });    
    }, [])

    const logOut = () => {
        axios.get(`https://api.stackexchange.com/2.2/apps/${token}/de-authenticate?key=veSvJe2FSlo65dBocEff1Q((`)
            .then(() => {
                setLoggedIn(false);
            })
        console.log('log out');
    }

    return (
        <header >
            <div className='textLogo'>
                <Link to="/"> <img src={logo} alt="logo" id="logo" /></Link>
                <span>stack</span>
                <span className='boldText'>overflow uSof</span>
            </div>
            <div className="search">
                <input type="text" name="search" placeholder="Search ..." id="searchInput" />
                <button type="submit" id="searchButton">GO</button>
            </div>

            {loggedIn ? <LoggedHeader token={token} /> : ''}

            <button className='logIn' onClick={loggedIn ? logOut : auth} disabled={isNotLogged}> {loggedIn ? 'Log out' : 'Log in'} </button>
        </header>
    );
}

export default Header;